.header-container {
    display: flex;
    justify-content: space-between;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: rgba(0, 0, 0, .125);

    .header-button {
        display: flex;
        font-family: Montserrat, helvetica neue, Helvetica, Arial, sans-serif;
        font-weight: 700;
        color: #2e2e2e;
        letter-spacing: 0.2em;
        line-height: 1.75rem;
        align-items: center;
        padding: 6px;

        &:hover {
            color: #0096cc;
            cursor: pointer;
            background-color: #E8F2F6;
        }
    }

    .name {
        font-size: 16px;
    }

    .menu {
        font-size: 12px;

    }

    .uri {
        padding-left: 12px;
        padding-right: 12px;

    }

    .link {
        color: inherit;
        text-decoration: none;
        font-size: 12px;

    }

    .menus {
        display: flex;
    }



    a:-webkit-any-link {
        color: #2e2e2e;
        cursor: pointer;
        text-decoration: none;
        font-style: normal;
    }
}